import "./success.css";

const Success = () => {
  return (
    <div style={{height: "80%", position: "absolute", bottom: "40%"}} className="d-flex success">
      <div style={{width: "30%", backgroundColor: "#034ea2", overflowY: "scroll", borderTopLeftRadius: "15px", borderBottomLeftRadius: "15px"}} className="p-5 text-light">
        <h4>Our Success Stories</h4>
        <p>
          Our superlative cardiac experts handle the most delicate of cardiac
          cases across all ages. Their range of treatment expertise includes the
          entire spectrum of cardiac conditions including congenital heart
          disease, coronary artery disease, heart rhythm disorders, disorders of
          heart failure, and valvular disease. Best in their speciality, our
          team of eminent echo cardiologists, electro physiologists,
          interventional cardiologists, radiologists and cardiovascular surgeons
          are skilled in treating the most challenging of heart conditions
          including paediatric surgeries even in new born.
        </p>
      </div>
      <div style={{width: "70%", opacity: "0.8"}} className="p-2 bg-dark rounded-end trans"></div>
    </div>
  );
};
export default Success;
