import axios from "axios";
import { useState, useEffect } from "react";
import { Carousel } from "react-bootstrap";
import "./slider.css";
import { useSelector, useDispatch } from "react-redux";
import { postNum, postDate } from "./actions/index.js";

const Slider = () => {
  let timeStr = new Date().toISOString();
  timeStr = timeStr.substr(0, 10);
  const myState = useSelector((state) => state.postData);
  const dispatch = useDispatch();
  const [time, setTime] = useState(false);
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [date, setDate] = useState("");
  const submitHandler = async () => {
    let data = { name, phone };
    dispatch(postNum(data));
    let postData = await axios.post(
      "https://manipal-hospital.herokuapp.com/contact/create-lead",
      data
    );
    setTime(true);
  };
  const timeHandler = async () => {
    let timeData = { phone: myState.phone, date };
    dispatch(postDate(timeData));
    let postTimeData = await axios.patch(
      "https://manipal-hospital.herokuapp.com/contact/book-slot",
      timeData
    );
  };
  return (
    <div style={{ position: "relative" }}>
      <Carousel>
        <Carousel.Item>
          <img
            className="d-block w-100"
            src="https://i.postimg.cc/sDC6pGj0/main.webp"
          />
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100"
            src="https://i.postimg.cc/yYnmkDkR/main1.jpg"
          />
        </Carousel.Item>
      </Carousel>
      {time ? (
        <form
          style={{ backgroundColor: "#fff" }}
          className="d-inline-block rounded shadow mt-5 book"
        >
        <span className="rounded p-2" style={{borderBottom: "3px solid darkgreen", position: "absolute", bottom: "100%", backgroundColor: "#90EE90"}}>Thankyou! We will reach out to you soon. Please select a suitable date for appointment.</span>
        <div className="p-4">
          <h4 className="fw-bolder mb-5 heading" style={{ color: "#207FDC" }}>
            Choose a Date
          </h4>
          <label className="mt-2">Time Slot</label>
          <br />
          <input
            type="date"
            className="w-100 pt-1 pb-2"
            min={timeStr}
            style={{
              border: "none",
              borderBottom: "2px solid lightgrey",
              outline: "none",
            }}
            value={date}
            onChange={(e) => setDate(e.target.value)}
            placeholder="+91 9000000000"
          />
          <br />
          <span className="w-100 d-flex justify-content-center mt-4">
            <input
              type="button"
              value="Submit"
              className="text-light px-5 py-1 rounded"
              style={{
                backgroundColor: "#207fdc",
                borderColor: "#207fdc",
                boxShadow: "none!important",
              }}
              onClick={timeHandler}
            />
          </span>
          </div>
        </form>
      ) : (
        <form
          style={{ backgroundColor: "#fff", top: 0 }}
          className="d-inline-block rounded shadow mt-5 p-4 book"
        >
          <h4 className="fw-bolder mb-5 heading" style={{ color: "#207FDC" }}>
            Book An Appointment
          </h4>
          <label className="mt-2">Full Name</label>
          <br />
          <input
            type="text"
            className="w-100 pt-1 pb-2"
            style={{
              border: "none",
              borderBottom: "2px solid lightgrey",
              outline: "none",
            }}
            value={name}
            onChange={(e) => setName(e.target.value)}
            placeholder="Your Name"
          />
          <br />
          <label className="mt-2">Contact No.</label>
          <br />
          <input
            type="number"
            className="w-100 pt-1 pb-2"
            style={{
              border: "none",
              borderBottom: "2px solid lightgrey",
              outline: "none",
            }}
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
            placeholder="+91 9000000000"
          />
          <br />
          <span className="w-100 d-flex justify-content-center mt-4">
            <input
              type="button"
              value="Submit"
              className="text-light px-5 py-1 rounded"
              style={{
                backgroundColor: "#207fdc",
                borderColor: "#207fdc",
                boxShadow: "none!important",
              }}
              onClick={submitHandler}
            />
          </span>
        </form>
      )}
    </div>
  );
};
export default Slider;
