import { Image } from "react-bootstrap";
import "./box.css";

function Box(props) {
  return (
    <div style={{ height: "16rem", backgroundColor: "#fff" }} className={`w-100 px-4 py-5 boxP shadow`}>
      <div className="box h-100">
        <Image
          style={{ width: "22%" }}
          src={props.link}
        />
        <h4 className="fw-bolder mt-4 text-dark" style={{fontFamily: 'Oswald, sans-serif'}}>
          {props.body}
        </h4>
      </div>
    </div>
  );
}
export default Box;
