const initialState = {};

const postData = (state = initialState, action) => {
    switch(action.type) {
        case "NUMBER": return state = {...state, name: action.payload.name, phone: action.payload.phone}; 
        case "DATE": return state = {...state, date: action.payload.date};
        default: return state; 
    }
} 

export default postData;